import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { ListPromoComponent } from './list-promo/list-promo.component';
import { HomeContainerComponent } from '../../shared/layout/container/home-container/home-container.component';

const routes: Routes = [
  {
    path: '',
    component: HomeContainerComponent,
    children: [
      {
        path: 'list-promo',
        component: ListPromoComponent,
        title: 'Promo-Terkini',
        data: {
          meta: {
            title: 'Promo Belanja Bulan Ini dan Paket Usaha  | Youtap BOS',
            description:
              'Promo belanja murah bulan ini dan paket usaha murah hanya di Youtap BOS. ',
            image:
              'https://storage.googleapis.com/production-youtap-public/banner/IlbfMVveQ=s0.png'
          }
        }
      }
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class PromoRoutingModule {}
