import { Component, OnInit } from '@angular/core';
import {
  HighlightProductVariantWarehouses,
  ListHighlightProductVariantWarehouseDto,
  ResponseListHighlightProductVariantWarehouseDto,
  SectionService
} from '@youtap-web-lib';
import { GaService } from '../../../shared/service/ga.service';
import { MetaService } from '../../../shared/service/meta.service';

@Component({
  selector: 'list-promo',
  templateUrl: './list-promo.component.html'
})
export class ListPromoComponent implements OnInit {
  loadingPromo = false;
  dataPromo!: ResponseListHighlightProductVariantWarehouseDto;
  listPromo: HighlightProductVariantWarehouses[] = [];
  loadMore = true;
  page = 1;

  constructor(
    private sectionService: SectionService,
    private gaService: GaService,
    private metaService: MetaService
  ) { }

  ngOnInit() {
    this.getDataPromoHighlight();

    this.metaService.setMeta({
      title: 'Promo Belanja Bulan Ini dan Paket Usaha  | Youtap BOS',
      description:
        'Promo belanja murah bulan ini dan paket usaha murah hanya di Youtap BOS. ',
      image:
        'https://storage.googleapis.com/production-youtap-public/banner/IlbfMVveQ=s0.png'
    });
  }

  getDataPromoHighlight() {
    this.loadingPromo = true;
    const data: ListHighlightProductVariantWarehouseDto = {
      listHighlightProductVariantInput: {
        page: this.page,
        limit: 20
      }
    };
    this.sectionService.listHighlightProductVariantWarehouse(data).subscribe({
      next: ({ data }) => {
        this.loadingPromo = false;
        this.dataPromo = data;
        data?.listHighlightProductVariantWarehouse?.highlightProductVariantWarehouses
          .filter((item) => item?.productVariantWarehouse?.stock !== 0)
          .filter((item) => item?.productVariantWarehouse !== null)
          .forEach((item) => {
            this.listPromo.push(item);
          });
      },
      error: (err) => {
        this.loadingPromo = false;
        return err;
      }
    });
  }

  onScroll() {
    this.page += 1;
    if (
      this.loadMore &&
      this.page <= this.dataPromo.listHighlightProductVariantWarehouse.totalPage
    ) {
      this.getDataPromoHighlight();
    }
  }

  goToDetail(slug: string) {
    this.gaService.addEvent('promo Detail ' + slug, 'promo', '');
  }
}
